<template>
  <div class="container" id="playContainer">
    <div id="circle" class="circle"></div>
    <div id="target" class="circle"></div>
  </div>
</template>

<script>
export default {
    mounted(){
  
    // Random position
    this.ranPosition()

    // Touch events
    window.ontouchmove = () => {
        var x = event.touches[0].pageX;
        var y = event.touches[0].pageY;
        let circle = document.querySelector('#circle');
        if(circle.offsetLeft < x + 25 && circle.offsetLeft > x - 55 && circle.offsetTop < y + 25 && circle.offsetTop > y - 55 ) {
        circle.style.left = x - 20 + 'px';
        circle.style.top = y - 20 + 'px';
        }
    }
  },
  methods: {
    ranPosition() {
      let circle = document.querySelector('#circle')
      let target = document.querySelector('#target')
      let xCircle = Math.floor(Math.random()*(90) + 5)
      let yCircle = Math.floor(Math.random()*(90) + 5)
      let xTarget = Math.floor(Math.random()*(90) + 5)
      let yTarget = Math.floor(Math.random()*(90) + 5)
      circle.style.left = xCircle + 'vw'
      circle.style.top = yCircle + 'vh'
      target.style.left = xTarget + 'vw'
      target.style.top = yTarget + 'vh'
    }
  }
} 
</script>

<style lang="sass">

.circle
  width: 50px
  height: 50px
  background-color: #fff
  border-radius: 50%
  opacity: 1
  box-shadow: 0px 0px 4px
  position: absolute

#target
  opacity: .5

#circle
  z-index: 500

</style>
